import React, { useState, useEffect } from 'react';

const API_BASE = process.env.REACT_APP_API_URL || "http://localhost:5001";

const AskQuestion = ({ categories, selectedCategory, onQuestionPosted }) => {
    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');
    const [category, setCategory] = useState(selectedCategory || categories[0]?.id || '');
    const [error, setError] = useState('');
    const [success, setSuccess] = useState('');

    useEffect(() => {
        setCategory(selectedCategory || categories[0]?.id || '');
    }, [selectedCategory, categories]);

    const handleSubmit = async (e) => {
        e.preventDefault();
        setError('');
        setSuccess('');
        const token = localStorage.getItem('token');
        const res = await fetch(`${API_BASE}/api/questions/`, {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                Authorization: token
            },
            body: JSON.stringify({ title, content, category })
        });
        const data = await res.json();
        if (res.ok) {
            setTitle('');
            setContent('');
            setSuccess('Question posted!');
            if (typeof onQuestionPosted === 'function') onQuestionPosted();
        } else {
            setError(data.message || 'There was a problem posting your question.');
        }
    };

    return (
        <form className="ask-question" onSubmit={handleSubmit}>
            <h3>Ask a Question</h3>
            {error && <div style={{ color: 'red' }}>{error}</div>}
            {success && <div style={{ color: 'green' }}>{success}</div>}
            <input
                type="text"
                placeholder="Title"
                value={title}
                onChange={e => setTitle(e.target.value)}
                required
            />
            <textarea
                placeholder="Your question..."
                value={content}
                onChange={e => setContent(e.target.value)}
                required
            />
            <select
                value={category}
                onChange={e => setCategory(e.target.value)}
                required
            >
                {categories.map(cat => (
                    <option key={cat.id} value={cat.id}>{cat.name}</option>
                ))}
            </select>
            <button type="submit">Post Question</button>
        </form>
    );
};

export default AskQuestion;